@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
html{font-size:72%;margin:0;padding:0}body,input,select,button{font-family:"Josefin Sans",sans-serif;font-size:1.6rem;margin:0;padding:0}input:focus,select:focus,button:focus{outline:none}.container{display:flex;flex-direction:row;width:97.5%;background-color:#f6ecc4;background-image:linear-gradient(315deg, #f6ecc4 0%, #f7d4d4 74%);border:.2rem solid #000;border-radius:.8rem;box-shadow:#444 4px 4px 18px 2px;margin:1.2rem;padding:3.2rem 0}.game_selector{display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;padding:.4rem 0}.game_logo{height:60px}.game_row{display:flex;flex-direction:row;align-items:center;justify-content:centers}.game_button{margin:1.2rem 1.2rem;width:9.6rem;height:3.2rem;background:#fff;color:#000;border:.2rem solid #000;transition:.2s;font-size:1.8rem;box-shadow:#444 4px 4px 10px 1px}.game_button:hover{background-color:#eec0c6;background-image:linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);color:#000;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.game_button_active{margin:1.2rem 1.2rem;width:9.6rem;height:3.2rem;border:.2rem solid #000;transition:.2s;font-size:1.8rem;background-color:#eec0c6;background-image:linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);color:#000}.game_reset_button{margin:1.2rem 1.2rem;width:8.4rem;height:3.2rem;background-color:#f6ecc4;background-image:linear-gradient(315deg, #f6ecc4 0%, #f7d4d4 74%);color:#000;border:.2rem solid #000;transition:.2s;font-size:1.8rem;box-shadow:#444 4px 4px 10px 1px}.game_reset_button:hover{background-color:#ffcb43;background-image:linear-gradient(319deg, #ffcb43 0%, #ff6425 37%, #ff0016 100%);color:#fff;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.game_checkbox{margin-left:.8rem;width:25px;height:25px}.game_checkbox:hover{-webkit-transform:scale(1.2);transform:scale(1.2);cursor:pointer}.game_status{padding-top:2px;font-size:2rem}.game_reset_icon_container{display:flex;align-items:center;justify-content:center}.game_reset_icon{margin-left:.4rem;width:15px}.score_board{display:flex;flex-direction:row;align-items:center;justify-content:space-around;padding:.2rem .2rem}.score_board_row{display:flex;flex-direction:row;align-items:center;justify-content:center;margin-right:60px}.score_new_button{width:9.6rem;padding:.4rem .2rem;background-color:#f6ecc4;background-image:linear-gradient(315deg, #f6ecc4 0%, #f7d4d4 74%);color:#000;border:.2rem solid #000;transition:.2s;font-size:1.8rem;margin:0 3.2rem;box-shadow:#444 4px 4px 10px 1px}.score_new_button:hover{background-color:#ffcb43;background-image:linear-gradient(319deg, #ffcb43 0%, #ff6425 37%, #ff0016 100%);color:#fff;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.score_new_icon_container{display:flex;align-items:center;justify-content:center;flex-wrap:wrap}.score_new_icon{margin-left:.4rem;width:25px;height:25px}.score_button{padding-top:.8rem;width:6.4rem;background-color:#f6ecc4;background-image:linear-gradient(315deg, #f6ecc4 0%, #f7d4d4 74%);color:#000;border:.2rem solid #000;border-radius:.8rem;transition:.2s;font-size:3.6rem;box-shadow:#444 4px 4px 10px 1px}.score_button_win{background-color:#08e1ae;background-image:linear-gradient(315deg, #08e1ae 0%, #98de5b 74%);color:#fff}.score_button_lose{background-color:#ffcb43;background-image:linear-gradient(319deg, #ffcb43 0%, #ff6425 37%, #ff0016 100%);color:#fff}.score_button:hover{background-color:#eec0c6;background-image:linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);color:#000;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.score_input{padding:1.2rem .4rem;width:13.8rem;height:3.2rem;background:#fff;color:#000;border:0 solid #000;transition:.2s;font-size:2.8rem;background-color:#ffffff00}.timer_container{display:flex;flex-direction:column;align-items:center;justify-content:center;width:15%}.timer_sub_container{display:flex;flex-direction:column;align-items:center;justify-content:center;border:.2rem dashed #000;border-radius:1.6rem;background-color:#fff;padding:2.8rem 1.4rem;box-shadow:#444 5px 5px 8px 3px}.timer_row{display:flex;align-items:center;justify-content:center}.timer_input{width:3.2rem;font-size:2rem;border:.2rem solid #000;padding:.4rem .4rem;margin:0 1.2rem}.timer_number{margin:2rem 0;color:#000;font-size:6.4rem;border:0 solid}.timer_button{margin:0 .8rem;width:3.2rem;height:3.2rem;background:#fff;border:0 solid #000;transition:.3s;font-size:1.8rem}.timer_button:hover{-webkit-transform:scale(2.2);transform:scale(2.2);cursor:pointer}.timer_button_play{color:green}.timer_button_pause{color:red}.timer_button_resume{color:orange}.image_row{display:flex;align-items:center;justify-content:center;width:40%}.image{display:flex;flex-wrap:wrap;width:400px;height:500px;border:.2rem solid #000;box-shadow:#444 5px 5px 8px 3px}.image_button{width:50px;height:50px;background-color:#b8c6db;background-image:linear-gradient(315deg, #b8c6db 0%, #f5f7fa 74%);color:#000;transition:.3s;font-size:1.8rem;border:1px solid #000;box-shadow:#444 5px 5px 8px 3px}.image_button:hover{background-color:#08e1ae;background-image:linear-gradient(315deg, #08e1ae 0%, #98de5b 74%);color:#fff;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.image_button_closed{width:50px;height:50px;background:#ffffff00;color:#ffffff00;font-size:0;border:0 solid #000}.number_button_row{display:flex;align-items:center;justify-content:center;flex-direction:row;flex-wrap:wrap;width:45%}.number_button{margin:1.2rem 1.2rem;width:9.6rem;height:9.6rem;background-color:#b8c6db;background-image:linear-gradient(315deg, #b8c6db 0%, #f5f7fa 74%);color:#000;border:.2rem solid #000;transition:.3s;font-size:6.4rem;border-radius:4.8rem;padding-top:1.2rem;box-shadow:#444 5px 5px 8px 3px}.number_button:hover{background-color:#08e1ae;background-image:linear-gradient(315deg, #08e1ae 0%, #98de5b 74%);color:#fff;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.number_button_closed{margin:1.2rem 1.2rem;width:9.6rem;height:9.6rem;background-color:#ffcb43;background-image:linear-gradient(319deg, #ffcb43 0%, #ff6425 37%, #ff0016 100%);color:#ff8e61;border:0;transition:.3s;font-size:6.4rem;border-radius:4.8rem;padding-top:1.2rem}.ReactModalPortal .ReactModal__Overlay{align-items:center;display:flex;justify-content:center;transition:opacity 200ms ease-in-out}.result_modal{border:.4rem dashed #000;border-radius:3.2rem;box-shadow:#444 4px 4px 18px 2px;margin:7.2rem 1.2rem;outline:none;padding:3.2rem;background-color:#f6ecc4;background-image:linear-gradient(315deg, #f6ecc4 0%, #f7d4d4 74%)}.result_status{font-size:11.4rem;color:green}
